<template>
  <div id="scroll">
    <!-- 日租房、长短租 -->
    <mt-navbar v-model="selected">
      <mt-tab-item
        id="1"
        @click.native="btnSearch(1)"
      >
        <span>日租</span>
      </mt-tab-item>
      <mt-tab-item
        id="2"
        @click.native="btnSearch(2)"
      >
        <span>长短租</span>
      </mt-tab-item>
    </mt-navbar>
    <div class="status_box">
      <span>结算日</span>
      <span>金额</span>
    </div>
    <!-- 月份 -->
    <div>
      <div
        @click="month_statu = !month_statu"
        class="all"
      >
        <div style="text-align:left;width:70%;">
          <div
            style="font-weight:bold;display: flex;align-items: center;"
            @click.stop="goNext('ym',$route.query.billTime)"
          >{{ $route.query.billTime | dataFilter('YYYY年MM月') }}
            <van-icon name="arrow" />
          </div>
          <div
            class="money"
            v-if="d_order || d_other"
          >
            <div style="display:inline-block;text-align:left;">
              <div style="color:#000;font-weight:bold;">结算金额：<span style="color:#ff7d41;"> ¥{{ Number(d_order - d_other).toFixed(2) }}</span></div>
              <div style="color:#888;display:flex;font-size: 12px;">
                <div>总收入：¥{{ Number(d_order).toFixed(2) }}</div>&ensp;
                <div>总支出：¥{{ Number(d_other).toFixed(2) }}</div>
              </div>
            </div>
          </div>
        </div>
        <van-icon :name="listArr.length > 0 && month_statu == true?'arrow-down':'arrow'" />
      </div>
      <div v-if="listArr.length > 0 && month_statu == true">
        <div
          v-for="(item,index) in listArr"
          :key="index"
          class="month_style"
          @click="goNext('ymd',item)"
        >
          <div>{{ item.billTime }}</div>
          <div class="c_00">{{ Number(item.balanceAmount).toFixed(2) }}
            <van-icon name="arrow" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { GetbusinessLandlordBillRecordBalance, GetbusinessLandlordBillRecordMonthBalance } from '@/api/api_landlord';
export default {
  components: {},
  data() {
    return {
      selected: '1',
      key: 0,
      listArr: [],
      item: '',
      statu: false,
      options: {},
      priceAll: {},
      contentKey: 0,
      d_order: null,
      d_other: null,
      month_statu: true,
      num: '1'
    };
  },
  created() {
    this.$store.commit('setHeader', '业主-' + this.$route.query.landlord);
    this.getInfo()
  },
  methods: {
    // 切换
    btnSearch(num) {
      this.getInfo()
    },
    // 日租-or-长租
    getInfo() {
      let data = {
        billTime: this.$route.query.billTime,
        landLordId: this.$route.query.landLordId,
      };
      let _api = this.selected == '1' ? GetbusinessLandlordBillRecordBalance : GetbusinessLandlordBillRecordMonthBalance
      _api(data).done((res) => {
        this.listArr = [];
        this.d_order = res.order   // 总收入
        this.d_other = res.other   // 支出
        for (const k in res.data) {
          let obj = {}
          obj.billTime = k
          obj.balanceAmount = res.data[k]
          this.listArr.push(obj)
        }
        this.listArr.sort(function (a, b) {
          return a.billTime > b.billTime ? -1 : 1
        })
      })
    },
    func(options) {
      options.sort(function (a, b) {
        return a.billTime > b.billTime ? -1 : 1
      })
      options.map(item => {
        this.priceAll[ym + '-come'] = Number(this.order).toFixed(2)
        this.priceAll[ym + '-out'] = Number(this.other).toFixed(2)
        ++this.key;
      })
    },
    // 已出账---长租和日租---日账单
    goNext(type, item) {
      this.$router.push({
        path: '/owner-settle-finance-detail',
        query: {
          billTime: type == 'ym' ? this.$route.query.billTime : item.billTime,
          selected: this.selected,
          landLordId: this.$route.query.landLordId,
          landlord: this.$route.query.landlord
        }
      });
    }
  }
};
</script>

<style scoped>
#scroll {
    padding: 15px 0;
    position: fixed;
    top: 50px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
    background-color: #f6f6f6;
    font-size: 15px;
    color: #000;
}
.status_box {
    margin: 10px 0;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    padding: 15px 30px;
    font-weight: bold;
}
ul {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
.c_00 {
    color: rgb(0, 0, 0);
    display: flex;
    align-items: center;
}
.all {
    background: #fff;
    display: flex;
    justify-content: space-between;
    padding: 8px 25px 8px 20px;
    align-items: center;
    line-height: 22px;
}
.money {
    font-size: 14px;
    display: flex;
}
.li-box {
    font-size: 15px;
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    padding: 4px 15px 4px 20px;
}
.other_box {
    color: #888888;
    line-height: 30px;
    border-bottom: 1px solid #f6f6f6;
}
select {
    border: solid 1px #000;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    border: none;
    outline: none;
}
select::-ms-expand {
    display: none;
}
.mint-navbar .mint-tab-item.is-selected {
    color: #ff7d41;
    margin-bottom: 0;
    border-bottom: 3px solid #ff7d41;
}
/* 年账单 */
.yearBill {
    line-height: 30px;
    text-align: left;
    padding-left: 30px;
    display: flex;
    align-items: center;
    color: #ff7d41;
}
.month_style {
    background: #fff;
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    border-bottom: 1px solid #eee;
}
/deep/ .van-tab {
    color: #989898;
}
/deep/ .van-tabs__line {
    width: 35%;
    background-color: #ff7d41;
}
/deep/ .van-tab--active {
    color: #ff7d41;
}
</style>



